body {
  font-family: 'PT Sans', sans-serif;
  background-color: #F19B2C;
  text-align: center;
  color: white;
  padding-left: 20%;
  padding-right: 20%;
}

h1 {
/*  margin-top: 10vh;*/
  font-size: 80px;
}

h2 {
  font-size: 24px;
}

h3 {
  font-size: 16px;
}

h4 {
  font-size: 20px;
  padding: 10px;
  max-width: 600px;
  margin: 0 auto;
  border: 1px solid white;
  margin-top: 5px;
  margin-bottom: 5px;
  border-radius: 10px;
  cursor: pointer;
}

h4:hover {
  background-color: #F1AE4C;
}

button {
  background-color: #F19B2C;
  color: white;
  cursor: pointer;
  display: block;
  margin: 0 auto;
}

button:hover {
  background-color: #F1AE4C;
}

.start-button, .reset-button {
  margin-top: 10vh;
  font-size: 24px;
  padding: 20px 30px 20px 30px;
  border-radius: 50px;
  border: 4px solid white;
}

.start-button {
  display: inline-block;
  margin-left: 20px;
  margin-right: 20px;
  width: 400px;
  margin-bottom: 50px;
}

.next-round-button {
  font-size: 20px;
  padding: 10px 20px 10px 20px;
  border-radius: 20px;
  border: 2px solid white;
  margin-top: 20px;
  margin-bottom: 20px;
}

.red-bg {
  background-color: #E64D43;
}

.red-bg:hover {
  background-color: #E64D43;
}

.green-bg {
  background-color: #2eb476;
}

.green-bg:hover {
  background-color: #2eb476;
}

.no-hover:hover {
  background-color: #F19B2C;
}

.google-map {
  height: 300px;
  width: 100%;
  max-width: 650px;
}
.google-map-container {
  margin-top: 30px;
  margin-bottom: 30px;
  height: 300px;
}

.progress-bar-container {
  width: 50%;
  height: 20px;
  margin: 0 auto;
  background-color: #F19B2C;
  -webkit-box-shadow: inset 0 0 20px #a56d31;
  box-shadow:         inset 0 0 20px #a56d31;
  border-radius: 20px;
}

.progress-bar {
  background-color: #2eb476;
  -webkit-box-shadow: inset 0 0 20px #27895c;
  box-shadow:         inset 0 0 20px #27895c;
  height: 20px;
  width: 50%;
  border-radius: 10px;
}

.cover-image {
  width: 70%;
}

@media (max-width: 599px) {
  body {
    padding-left: 5%;
    padding-right: 5%;
  }
  h1 {
    font-size: 40px;
  }
  h2 {
    font-size: 16px;
  }
  h3 {
    font-size: 16px;
  }
  h4 {
    font-size: 16px;
    padding: 10px;
  }
  .start-button, .reset-button {
    margin-top: 20px;
    margin-bottom: 0;
    font-size: 20px;
    padding: 20px 20px 20px 20px;
    width: 300px;
    border: 2px solid white;
  }
  .next-round-button {
    font-size: 16px;
  }
  .google-map {
    height: 240px;
    width: 100%;
  }
  .google-map-container {
    margin-top: 30px;
    margin-bottom: 30px;
  }

  .progress-bar, .progress-bar-container {
    width: 90%;
  }
  .topline {
    display: inline-block;
    padding-right: 20px;
  }
  .cover-image {
    width: 90%;
}

  }


